@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: normal;
  src: url("assets/fonts/montserrat-alternates/regular.ttf") format("truetype"); }

@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: bold;
  src: url("assets/fonts/montserrat-alternates/bold.ttf") format("truetype"); }

@font-face {
  font-family: "Montserrat Alternates";
  font-style: italic;
  font-weight: normal;
  src: url("assets/fonts/montserrat-alternates/italic.ttf") format("truetype"); }

@font-face {
  font-family: "Montserrat Alternates";
  font-style: italic;
  font-weight: bold;
  src: url("assets/fonts/montserrat-alternates/bolditalic.ttf") format("truetype"); }

body, html {
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 16px;
  line-height: normal;
  font-weight: 400;
  color: #4a4a4a;
  background: #fafafa;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  padding-bottom: 300px; }

.wrapper {
  width: 1230px;
  margin-left: auto;
  margin-right: auto; }

.app {
  padding-top: 50px; }
  .app .el-counter {
    margin-bottom: 15px; }

button {
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s; }
  button:active {
    -webkit-transform: scale(0.9);
    -ms-transform: scale(0.9);
    transform: scale(0.9); }



